@font-face{
    font-family: EconomicaBold;
    src: url(assets/economica-bold.ttf);
    font-weight: 800;
}

@font-face{
    font-family: RobotoCondensed;
    src: url(assets/roboto-condensed.light.ttf);
}

body{
    text-align: center;
    font-size: 20px;
    padding: 2.5%;
    font-family: 'RobotoCondensed';
}

h1,h2,h3,h4,h5{
    font-family: 'EconomicaBold';
}

.track{
    width: 250px;
    height: 250px;
    position: relative;
    cursor: pointer;
    margin: 20px;
    display: inline-block;
}

.track-image{
    width: 250px;
    height: 250px;
}

.track-name{
    background-color: black;
    color: white;
    padding: 5px;
    position: absolute;
    width: 250px;
    bottom: 0px;
}

.track-icon{
    position:absolute;
    bottom: 100px;
    left: 100px;
    color:white;
    background-color: black;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    padding: 10px;
    opacity: 0.8;

}
